// Carousel with text 
.carousel {
    .slick-slide {
        position: relative;
        img {
            width:100%;
        }
    }
    .carousel-text-area {
        position: absolute;
        top: 30px;
        left: 60px;
        color: #fff; //@white;
        text-shadow: 0px 0px 5px #323334;
        display: none;
        @media screen and (min-width: 768px) {
            display:block;
            left: 60px;
        }
        @media screen and (min-width: 992px) {
   
            left: 60px;
        }
        h1 {
            text-transform:uppercase;
            color:inherit;
            line-height: 1;
        }

        p {
            line-height: 1.4;
            a {
                font-size: 18px; //@font-size-large
            }
        }
    }
}


